body {
    padding: 0;
    margin: 0;
}

.Header {

    &-inclusive {
        background: url("/img/arkaplan.png") center center no-repeat;
        background-size: cover;
        width: 100%;

        @include mq() {
            overflow-x: hidden;
            width: 100%;
            display: flex;
            align-items: center;
            justify-content: center;

            height: 100vh;
        }
    }

    &-logo {
        position: absolute;
        left: 110px;
        top: 37px;
        transition: 0.5s;

        @include mq() {
            position: absolute;
            left: 15px;
            top: 30px;
            transition: 0.5s;
        }
    }

    &-title {
        width: 90%;
        height: 800px;
        display: flex;
        margin-left: auto;
        margin-right: auto;
        align-items: center;
        justify-content: center;

        @include mq() {
            height: 0;
            padding: 0;
            margin: 0;
        }

        &-svg {
            width: 66.666667%;

            @include mq() {
                width: 94%;
            }
        }
    }
}