.Footer {
    margin-top: 200px;

    .container {
        display: flex;
        height: 250px;
        width: 100%;
        background-color: #2D2D2D;

        @include mq() {
            height: 800px;
        }

    }

    .content {
        max-width: 1200px;
        width: 100%;
        display: flex;
        flex-direction: column;
        margin-left: auto;
        margin-right: auto;

        @include mq() {
            display: flex;
            margin-top: 80px;
        }
    }

    .Footer-ground {
        display: flex;
        align-items: center;
        margin-top: auto;
        margin-bottom: auto;


        @include mq() {
            display: flex;
            flex-direction: column;
            margin-left: auto;
            margin-right: auto;
        }

        .yardim {
            max-width: 84px;
            width: 100%;
            padding-right: 50px;
            border-right: 2px solid white;
            box-sizing: content-box;

            @include mq () {
                padding: 0;
                border: 0;
                display: flex;
                margin-left: auto;
                margin-right: auto;
                max-width: 150px;
                border-bottom: 2px solid white;
                padding-bottom: 50px;
            }
        }

        .iyilik {
            padding-left: 50px;

            @include mq() {
                padding: 0;
                margin-top: 50px;
                max-width: 450px;
                width: 100%;
                border-bottom: 2px solid white;
                padding-bottom: 50px;
            }

        }

    }

    &-text {
        display: flex;
        flex-direction: column;
        margin-right: 0;
        margin-left: auto;
        gap: 20px;
        font-family: "Inter";


        @include mq() {
            margin-bottom: 50px;
            margin-top: 50px;
            gap: 20px;
            text-align: center;
        }

        a {
            text-decoration: none;
            color: white;
            padding: 0;

            &:hover {
                text-decoration: underline;
                color: #80BC00;
            }
        }
    }
}