@mixin mq($width: "tablet") {
    @if ($width =="tablet") {
        @media (max-width: 768px) {
            @content
        }
    }

    @else if ($width =="desktop") {
        @media (min-width: 992px) {
            @content
        }
    }

    @else if ($width =="large") {
        @media (min-width: 1200px) {
            @content
        }
    }

    @else if ($width =="Ã¶zel") {
        @media (max-width: 925px) {
            @content
        }
    }
}