* {
    box-sizing: border-box;
}

.Article {
    .container {
        max-width: 1200px;
        width: 100%;
        margin-left: auto;
        margin-right: auto;
    }

    &-title {
        color: #80BC00;
        font-family: "Quicksand";
        margin-top: 150px;
        margin-bottom: 50px;
        display: flex;
        justify-content: center;

        @include mq() {
            font-size: 2.5em;
            margin-bottom: 0px;
        }
    }

    .proje {
        display: flex;
        flex-direction: column;
    }

    &-card {
        font-family: "Inter";
        max-width: 600px;
        width: 100%;
        height: 150px;
        background-color: rgba(#80BC00, .2);
        color: #80BC00;
        border-radius: 30px;
        display: flex;
        align-items: center;
        justify-content: center;
        padding: 20px 30px;
        text-align: center;
        margin-top: 50px;
        margin-bottom: 50px;

        @include mq() {
            margin-top: 150px;
            margin-bottom: 150px;
            display: flex;
            flex-direction: column;
        }
    }
}

