.panel2 {
    display: flex;
    width: 112px;
    height: 87px;
    position: absolute;
    left: 240px;

    @include mq() {
        position: relative;
        left: 0;
        bottom: 230px;
    }
}

.surec1 {
    display: flex;
    justify-content: center;
    margin-left: auto;
    margin-right: auto;
    align-items: center;
    position: relative;
    width: 100%;

    @include mq() {
        position: absolute;
        height: 100px;
        width: 100px;
    }
}

.surec1::before {
    content: "";
    display: block;
    height: 180px;
    width: 10px;
    position: absolute;
    bottom: 40px;
    background-color: rgba(123, 152, 206, .45);
    z-index: -1;

    @include mq() {
        position: absolute;
        bottom: 125px;
        width: 100%;
        height: 120px;
        width: 10px;
    }
}

.surec1::after {
    content: "";
    display: block;
    width: 100%;
    height: 10px;
    background-color: rgba(123, 152, 206, .45);
    z-index: -1;

    @include mq() {
        position: absolute;
        height: 180px;
        width: 10px;
        bottom: 245px;
    }
}

.surec2 {
    display: flex;
    align-items: center;
    position: relative;
    width: 100%;
    max-width: 350px;
    justify-content: center;


    @include mq() {
        position: absolute;
        height: 100px;
        width: 100px;
    }
}

.surec2::before {
    content: "";
    max-width: 320px;
    width: 100%;
    height: 10px;
    background-color: rgba(123, 152, 206, .45);
    position: absolute;
    right: 35px;

    @include mq() {
        position: absolute;
        right: auto;
        height: 180px;
        width: 10px;
        bottom: 245px;
    }
}

.surec2::after {
    content: "";
    display: block;
    height: 150px;
    width: 10px;
    position: absolute;
    left: 300px;
    bottom: 25px;
    z-index: -1;

    background-color: rgba(123, 152, 206, .45);

    @include mq() {
        position: absolute;
        height: 120px;
        width: 10px;
        bottom: 125px;
        left: 45px;
    }
}

.p-text {
    font-size: 0.7em;

    @include mq() {
        max-width: 800px;
        width: 100%;
        font-size: 0.7em;
    }
}

