.Section {
    &-area {
        margin-top: 75px;
        width: 100%;
        max-width: 1000px;
        margin-left: auto;
        margin-right: auto;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;


        @include mq() {
            max-width: 600px;
            width: 100%;
        }

        &-foto {
            display: flex;
            align-items: center;
            justify-content: center;
            position: relative;

            &-svg {

                .svg1 {
                    position: absolute;
                    bottom: 120px;
                    left: -80px;
                    z-index: -1;

                    @include mq() {
                        display: none;
                    }
                }

                .svg2 {
                    position: absolute;
                    left: 590px;
                    top: 250px;
                    z-index: -1;

                    @include mq() {
                        width: 78px;
                        height: 123px;
                        top: 200px;
                        left: 400px;
                        display: none;
                    }
                }
            }

            &-img {
                margin-top: 60px;
                width: 100%;
                height: 261px;
                display: flex;
                justify-content: center;
                align-items: center;
                margin-top: 120px;
                position: relative;
                box-sizing: border-box;

                @include mq() {
                    width: 100%;
                }

                .resim {
                    width: 100%;
                    height: 400px;

                    @include mq() {
                        width: 100%;
                        height: 250px;
                    }
                }
            }
        }
    }

    &-text {
        display: flex;
        flex-direction: column;
        width: 100%;
        max-width: 800px;

        @include mq() {
            text-align: center;
        }

        &-paragraph {
            color: #737984;
            text-align: center;
            font-family: 'Inter', sans-serif, Arial;

            @include mq() {
                text-align: center;
                padding-left: 20px;
                padding-right: 20px;
            }
        }
    }
}

.icon {
    position: absolute;
    left: 299px;
    top: 93px;
    height: 75px;
    width: 75px;
    background-color: rgba(238, 238, 238, 0.232);
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 100px;

    @include mq() {
        position: absolute;
        left: 180px;
    }
}

.simsek,
.yaprak,
.svg1,
.svg2 {
    opacity: 0.5;
}

h1 {
    font-size: 3.5em;
    text-align: center;
}